@import url(https://db.onlinewebfonts.com/c/4cc9d692753925335e7af83ecf6e6815?family=MuseoW01-900);
@font-face {
  font-family: "MuseoW01-900";
    src: url(https://plug.willyouplease.me/tribute/static/media/MuseoW01-900.57f7a004.woff2) format("woff2"), 
    url(https://plug.willyouplease.me/tribute/static/media/MuseoW01-900.4cc9d692.ttf) format("truetype"), /* chrome firefox opera Safari, Android, iOS 4.2+*/
}

*, *:before, *:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "MuseoW01-900";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

